import React, { useEffect, useState } from "react";
import { Button, Card, Col, Form, Row } from "react-bootstrap";
import { BiSearch } from "react-icons/bi";
import { AiFillEye } from "react-icons/ai";
import { useNavigate } from "react-router-dom";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import "react-datepicker/dist/react-datepicker.css";
import { PATH } from "../../../../utils/path";
import {
  adminFormStatus,
  getAllAdminPresForm,
} from "../../../../app/features/OnlinePrescriptionForm/OnlinePrescriptionForm.slice";
import { useDispatch, useSelector } from "react-redux";
import FormModal from "../../../../components/Modal/FormModal";
import FullScreenLoader from "../../../../authorization/FullScreenLoader";
import { useForm } from "react-hook-form";
import { createFee, getFee } from "../../../../app/features/fee/fee.slice";

export default function FormsDatatable() {
  const { register, handleSubmit, reset } = useForm();

  const [pageNumber, setPageNumber] = useState(1);

  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);

  const [searchValue, setSearchValue] = useState();

  const dispatch = useDispatch();
  const navigate = useNavigate();
  const user = JSON.parse(localStorage.getItem("family_doc_app"));
  const { getAllAdminOnlinePrescripionForm, isLoading, isError, isSuccess } =
    useSelector((state) => state?.onlinePrescriptionForm || []);

  const {feeData} = useSelector((state) => state?.fee);
  const totalRecords = getAllAdminOnlinePrescripionForm?.totalCounts;

  const handleButtonClick = () => {
    if (user?.roleId === 1) {
      navigate(PATH.SUPERADMIN_QUESTIONNAIRE);
    } else if (user?.roleId === 4) {
      navigate(PATH.ADMIN_QUESTIONNAIRE);
    }
  };

  const generateLinkTo = (data) => {
    if (user?.roleId === 1) {
      navigate(`${PATH.SUPERADMIN_QUESTIONNAIRE}?disease=${data?.formId}`);
    } else if (user?.roleId === 4) {
      navigate(`${PATH.ADMIN_QUESTIONNAIRE}?disease=${data?.formId}`);
    }
  };

  useEffect(() => {
    const finalData = {
      Search: searchValue ? searchValue : "",
      PageNo: pageNumber,
      Size: paginateOptions.paginationSize,
    };
    dispatch(getAllAdminPresForm(finalData));
  }, [dispatch, searchValue]);

  function moveToNext() {
    const finalData = {
      Search: searchValue ? searchValue : "",
      PageNo: pageNumber,
      Size: paginateOptions.paginationSize,

    };
    dispatch(getAllAdminPresForm(finalData));
  }

  function handleFormStatus(formId, event) {
    let finalData = {
      formId: formId,
    };
    dispatch(adminFormStatus({ finalData, moveToNext }));
  }

  const columns = [
    {
      dataField: "formId",
      text: "ID",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "title",
      text: "Disease Name",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "createdDate",
      text: "Created Date",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "isActive",
      text: "Status",
      sort: false,
      headerStyle: { backgroundColor: "#F1F1F1" },
      headerAlign: "center",
      formatter: (row, data) => (
        <p
          className={`mb-0 text-center py-1 custom-width-class ${
            data?.isActive ? "active-status" : "inactive-status"
          }`}
        >
          {data?.isActive ? "Active" : "Inactive"}
        </p>
      ),
    },
    {
      dataField: "action",
      text: "Action",
      sort: false,
      headerAlign: "center",
      formatter: (row, data) => {
        return (
          <div className="d-flex justify-content-evenly align-items-center">
            <AiFillEye
              data-toggle="tooltip"
              data-placement="top"
              title="View and Edit"
              onClick={() => generateLinkTo(data)}
              className="table-action text-cursor-pointer"
            />
            <Form.Check
              type="switch"
              id={`switch-${data?.formId}`}
              checked={data?.isActive === false ? false : true}
              onChange={(event) => handleFormStatus(data?.formId, event)}
            />
          </div>
        );
      },
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
  ];

  const customTotal = (from, to, size) => (
    <span className="react-bootstrap-table-pagination-total">
      <b> {from} </b> to <b>{to}</b> out of <b>{size} entries</b>
    </span>
  );

  const paginateOptions = {
    paginationSize: 10,
    pageStartIndex: 1,
    alwaysShowAllBtns: true,
    withFirstAndLast: false,
    hideSizePerPage: true,
    showTotal: true,
    paginationTotalRenderer: customTotal,
    disablePageTitle: true,
    sizePerPageList: [
      {
        text: "10",
        value: 10,
      },
    ],
  };

  useEffect(() => {
    const finalData = {
      feeTypeId: 702,
    };
    dispatch(getFee(finalData));
  }, [dispatch, searchValue]);

  function onCreateSuccess() {
    const finalData = {
      feeTypeId: 702,
    };
    dispatch(getFee(finalData));
    handleClose();
    reset();
  }

  function createFeeData(submittedData) {
    const finalData = {
      feeId: feeData?.feeId ? feeData?.feeId : 0,
      value: parseFloat(submittedData?.feeValue),
      feeTypeId: 702,
      isActive: true,
    };
    dispatch(createFee({ finalData, onCreateSuccess }));
  }

  const handlePageChange = (page, sizePerPage) => {
    setPageNumber(page);
  };

  return (
    <>
      <Card className="shadow-sm patient-card patient-profile-wrapper">
        <>
          <div className="d-flex justify-content-between flex-wrap m-3">
            <span className="d-flex">
              <Form.Control
                type="text"
                placeholder="Search"
                className="me-2 search-field-spacing"
                aria-label="Search"
                onKeyDown={(e) => {
                  if (e.key === "Enter") {
                    setSearchValue(e.target.value);
                  }
                }}
                onChange={(e) => {
                  if (e.key === "Enter") {
                    setSearchValue(e.target.value);
                  }
                }}
              />
              <BiSearch size={22} className="searchbar-icon" />
            </span>
            <span>
              <Button
                variant="primary"
                type="button"
                className="Admin-Add-btn fw-bold me-2"
                onClick={() => {
                  setShow(true);
                }}
              >
                Create Fee
              </Button>
              <Button
                variant="primary"
                type="button"
                className="Admin-Add-btn fw-bold"
                onClick={handleButtonClick}
              >
                Create Form
              </Button>
            </span>
          </div>
          {isSuccess ? (
            <span className="doctor-datatable">
              {getAllAdminOnlinePrescripionForm?.data?.length > 0 ? (
                <BootstrapTable
                  columns={columns}
                  data={
                    getAllAdminOnlinePrescripionForm?.data
                      ? getAllAdminOnlinePrescripionForm?.data
                      : []
                  }
                  keyField="formId"
                  id="formId"
                  // pagination={paginationFactory(paginateOptions)}
                  pagination={paginationFactory({
                    ...paginateOptions,
                    page: pageNumber,
                    totalSize: totalRecords,
                    onPageChange: handlePageChange,
                  })}
                  // striped
                  remote
            onTableChange={() => {}}
                  bordered={false}
                  wrapperClasses="table-responsive"
                />
              ) : (
                <p className="text-center">No Record Found</p>
              )}
            </span>
          ) : isLoading ? (
            <FullScreenLoader />
          ) : isError ? (
            <p className="text-center">Network Error...</p>
          ) : null}
        </>
      </Card>

      <FormModal
        className="modal-lg"
        title="Create Form Fees"
        handleClose={handleClose}
        show={show}
      >
        <Form onSubmit={handleSubmit(createFeeData)}>
          <Row>
            <Col xs={12}>
              <Form.Group controlId="formBasicEmail">
                <Form.Label className="fw-bold">Fee (â‚¬)</Form.Label>
                <Form.Control
                  size="lg"
                  type="number"
                  step="0.01"
                  placeholder="Enter Fee (1.36)"
                  min={0}
                  {...register("feeValue", { required: true })}
                  required
                />
              </Form.Group>
            </Col>
            <div className="d-grid mt-4">
              <Button
                className="block"
                variant="primary primary_bg"
                size="lg"
                type="submit"
              >
                Create Fee
              </Button>
            </div>
          </Row>
        </Form>
      </FormModal>
    </>
  );
}
